.chat {
  display: flex;
  flex-direction: column;
  width: 680px;
  height: 720px;
  background-color: #f2f2f2;
  border-radius: 0 0 8px 0;
}
.chat .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e6e6e6;
  width: 100%;
  height: 55px;
}
.chat .header .title {
  margin-left: 24px;
  font-size: 16px;
  font-weight: 500;
  color: #18191a;
  line-height: 56px;
}
.chat .header .more-icon {
  margin-right: 8px;
  width: 40px;
  height: 40px;
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.chat .header .more-icon:hover {
  cursor: pointer;
  background-image: url(data:image/png;base64,);
}
.chat .message-content {
  flex-grow: 1;
