.right-click-box {
  z-index: 4;
  position: absolute;
  padding: 8px 0;
  width: 128px;
  background: #ffffff;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1), 0px 6px 12px 0px rgba(0, 0, 0, 0.04);
  border-radius: 4px;
}
.right-click-box .item {
  padding: 8px 0 8px 16px;
  font-size: 14px;
  font-weight: 400;
  color: #28292e;
}
.right-click-box .item:hover {
  cursor: pointer;
  color: #1169ff;
  background: #fafbfd;
}
  