* {
    font-family: PingFangSC-Medium, PingFang SC;
}
#zegoim {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    display: flex;
    justify-content: center;
    align-items: center;
}
.zego-im-container {
    display: flex;
    flex-direction: column;
    width: 1030px;
    height: 770px;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}
.top-banner {
    display: flex;
    box-sizing: border-box;
    padding-left: 24px;
    text-align: left;
    font-size: 16px;
    font-weight: 500;
    line-height: 50px;
    color: #fff;
    width: 100%;
    height: 50px;
    background-color: #3478fc;
    border-radius: 8px 8px 0 0;
}
.box {
    position: relative;
    display: flex;
    width: 100%;
    height: 720px;
}
.group-wrap {
    z-index: 4;
    position: absolute;
    right: 0;
    bottom: 0;
}